<template>
  <div id="q-app">
    <router-view />
  </div>
</template>

<script>

document.addEventListener('deviceready', () => {
  // nfc.showSettings((success) => {
  //   console.log(success)
  // }, (failure) => {
  //     console.log(failure)
  // });
}, false);

export default {
  name: 'App',

  data() {
    return {
      paymentMimeType: 'CryptoWallet/Payment',
      recievingAddress: 'hjdsgfhjdsgfjlhdsfajbkvgfkjhnas',
      nDefMimeType: 'cryptowallet/payments',
    };
  },

  beforeCreate() {
    window.nfc.addNdefListener(
      this.onNdef,
      () => {
        console.log('Listening for NDEF tags.');
      },
      () => {
        console.log('failed Listening for NDEF tags.');
      },
    );

    // Android reads non-NDEF tag. BlackBerry and Windows don't.
    window.nfc.addTagDiscoveredListener(
      this.onNfc,
      () => {
        console.log('Listening for non-NDEF tags.');
      },
      () => {
        console.log('failed Listening for non-NDEF tags.');
      },
    );

    // Android launches the app when tags with mime type text/pg are scanned
    // because of an intent in AndroidManifest.xml.
    // phonegap-nfc fires an ndef-mime event (as opposed to an ndef event)
    // the code reuses the same onNfc handler
    window.nfc.addMimeTypeListener(
      this.nDefMimeType,
      this.onNdef,
      () => {
        console.log('Listening for NDEF mime tags with type text/pg.');
      },
      () => {
        console.log('failed Listening for NDEF mime tags with type text/pg.');
      },
    );

    // window.nfc.addMimeTypeListener(this.nDefMimeType, (tagData) => {
    //   console.log(tagData);
    // }, () => {
    //   console.log('listening for specific mime type tags');
    // }, () => {
    //   console.log('unable to listen');
    // });
  },

  created() {
    console.log(this.$q);
    console.log(window);
    console.log(window.nfc);
    console.log(window.ndef);
    const record = window.ndef.mimeMediaRecord(
      this.nDefMimeType,
      window.nfc.stringToBytes(this.recievingAddress),
    );
    console.log(record);
  },

  beforeMount() {

  },

  updated() {
    //
  },

  methods: {
    onNdef(nfcEvent) {
      console.log(JSON.stringify(nfcEvent.tag));
      const tag = nfcEvent.tag;

      // BB7 has different names, copy to Android names
      if (tag.serialNumber) {
        tag.id = tag.serialNumber;
        tag.isWritable = !tag.isLocked;
        tag.canMakeReadOnly = tag.isLockable;
      }
      console.log(tag);
    },

    onNfc(nfcEvent) {
      const tag = nfcEvent.tag;
  
      console.log(JSON.stringify(nfcEvent.tag));
      
      console.log(tag);
    },
  }
};
</script>

<style>
</style>



